.App {
  display: grid;
  grid-template-columns: 250px 1fr 1fr;
  grid-template-rows: 100px 60px 1fr;
  gap: 0px 0px;
  grid-template-areas:
    'Header Header Header'
    'Search Search Search'
    'Content Detail Detail';
  width: 90vw;
  height: 100vh;
  margin: 0 auto;
  border: 1px rgb(141, 82, 18) solid;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.Header {
  display: flex;
  width: 100%;
  grid-area: Header;
  background-color: slategray;
  color: white;
}

.Header > h1 {
  margin: auto auto;
  text-align: center;
}

#search-section {
  grid-area: Search;
  display: flex;
  justify-content: center;
  align-content: center;
  align-items: center;
}

#searchResultSection {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-around;
  border: 1px blue solid;
  grid-area: Content;
  overflow: scroll;
  padding-top: 1rem;
}

.card {
  border: 1px solid black;
  margin: 0 1rem;
  margin-bottom: 1rem;
}

#search-form {
  width: 100%;
  height: 100%;
  display: flex;
  flex-wrap: wrap;
  align-content: center;
  justify-content: center;
}

#search-label {
  font-size: 1.3rem;
  margin-right: 1rem;
  line-height: 2;
}

#search-input {
  font-size: 1.3rem;
  padding-left: 10px;
}

#search-button {
  width: 100px;
  margin-left: 1rem;
}

.poster {
}

.showName {
  text-align: center;
  font-size: 1.4rem;
  padding: 2%;
}

.modal-hidden {
  visibility: hidden;
}

#detail-section {
  grid-area: Detail;

  border: 1px orangered solid;
}

#detail-section {
  display: grid;
  grid-template-columns: 50% 50%;
  grid-template-rows: 60px 1fr 1fr;
  gap: 0px 0px;
  grid-auto-flow: row;
  grid-template-areas:
    'title poster'
    'blurb poster'
    'chart chart';
}

#show-title {
  grid-area: title;
  margin: auto auto;
  font-size: 2rem;
}

#blurb-container {
  grid-area: blurb;
  margin: auto auto;
}
#blurb-container > p {
  text-align: center;
  margin: auto;
  font-size: 1.2rem;
}

#show-poster {
  grid-area: poster;
  margin: auto auto;
}
